body {
  margin: 0;
}

body * {
  font-family: "Montserrat", sans-serif !important;
}

p {
  font-family: "Questrial" !important;
}

body:before,
body:after {
  content: "";
  position: fixed;
  background: #ececec;
  left: 0;
  right: 0;
  height: 20px;
  z-index: 100;
}

body:before {
  top: 0;
}

body:after {
  bottom: 0;
}

body {
  border-left: 20px solid #ececec;
  border-right: 20px solid #ececec;
}

#footer {
  height: 200px;
  background-color: #ececec;
}

